                        query3="And combing the web for out-of-date insider ‘tips and tricks’ on one blog after another… STOP! You don’t have to anymore! Yes, SOMEONE needs to plan your vacation. In the words of the Cheshire Cat, “If you don’t know where you want to go, then it doesn’t matter which path you take.”"
                        query4="But that someone doesn’t have to be YOU!"
